import React, { Component } from 'react';
import axios from 'axios';


export default class EditParks extends Component {
    constructor(props){
        super(props);

        this.onChangeParkname = this.onChangeParkname.bind(this);
        this.onChangeParkinglots = this.onChangeParkinglots.bind(this);
        this.onChangeParkinglotsavailable = this.onChangeParkinglotsavailable.bind(this);
        this.onSubmit = this.onSubmit.bind(this);


        this.state = {
            parkname: '',
            parkinglots:0,
            parkinglotsavailable: 0
        }
    }

    componentDidMount(){
        axios.get('https://https-carparking.apps.us-east-2.starter.openshift-online.com/parks/'+this.props.match.params.id)
            .then(response => {
                this.setState({
                    parkname: response.data.parkname,
                    parkinglots: response.data.parkinglots,
                    parkinglotsavailable: response.data.parkinglotsavailable
                })
            })
            .catch(function (error){
                console.log(error);
            })
    }

    onChangeParkname(e){
        this.setState({
            parkname: e.target.value
        });
    }
    onChangeParkinglots(e){
        this.setState({
            parkinglots: e.target.value
        });
    }
    onChangeParkinglotsavailable(e){
        this.setState({
            parkinglotsavailable: e.target.value
        });
    }

    onSubmit(e) {
        e.preventDefault();
        const park = {
            parkname: this.state.parkname,
            parkinglots: this.state.parkinglots,
            parkinglotsavailable: this.state.parkinglotsavailable
        }
        if(this.state.parkinglots <= this.state.parkinglotsavailable){
            park.parkinglotsavailable = this.state.parkinglots;
        }
        
        axios.post('https://https-carparking.apps.us-east-2.starter.openshift-online.com/parks/update/'+this.props.match.params.id,park)
            .then(res => {
                console.log(res.data);
                window.location = '/carparking/';
            });
        
        
    }

    render(){
        return(
            <div>
                <h3>Editar estacionamento</h3> 
                <form onSubmit={this.onSubmit}>
                    <div className="form-group">
                        <label>Estacionamento: </label>
                        <input type="text"
                            required
                            className="form-control"
                            value={this.state.parkname}
                            onChange={this.onChangeParkname}
                            />
                    </div>
                    <div className="form-group">
                        <label>Total de Vagas: </label>
                        <input type="text"
                            required
                            className="form-control"
                            pattern="[0-9]*"
                            maxLength="4"
                            value={this.state.parkinglots}
                            onChange={this.onChangeParkinglots}
                            />
                    </div>
                    <div className="form-group">
                        <label>Vagas disponíveis: </label>
                        <input type="text"
                            required
                            className="form-control"
                            pattern="[0-9]*"
                            maxLength="4"
                            value={this.state.parkinglotsavailable}
                            onChange={this.onChangeParkinglotsavailable}
                            />
                    </div>
                    <div className="form-group">
                        <input type="submit" value="Salvar" className="btn btn-primary" />
                    </div>

                    <div className="form-group">
                        <label>Endpoints: </label>
                        <ol>
                            <li>
                                Buscar dados do estacionamento: (GET) <a href={'http://backend-carparking.apps.us-east-2.starter.openshift-online.com/parks/' + this.props.match.params.id}>http://backend-carparking.apps.us-east-2.starter.openshift-online.com/parks/{this.props.match.params.id}</a>
                            </li>
                            <li>
                                Liberar vaga no estacionamento: (POST) <a href={'http://backend-carparking.apps.us-east-2.starter.openshift-online.com/parks/free/' + this.props.match.params.id}>http://backend-carparking.apps.us-east-2.starter.openshift-online.com/parks/free/{this.props.match.params.id}</a>
                            </li>
                            <li>
                                Preencher vaga no estacionamento: (POST) <a href={'http://backend-carparking.apps.us-east-2.starter.openshift-online.com/parks/fill/' + this.props.match.params.id}>http://backend-carparking.apps.us-east-2.starter.openshift-online.com/parks/fill/{this.props.match.params.id}</a>
                            </li>
                            <li>
                                Apagar estacionamento: (DELETE) <a href={'http://backend-carparking.apps.us-east-2.starter.openshift-online.com/parks/' + this.props.match.params.id}>http://backend-carparking.apps.us-east-2.starter.openshift-online.com/parks/{this.props.match.params.id}</a>
                            </li>
                            <li>
                                Editar dados do estacionamento: (POST) <a href={'http://backend-carparking.apps.us-east-2.starter.openshift-online.com/parks/update/' + this.props.match.params.id}>http://backend-carparking.apps.us-east-2.starter.openshift-online.com/parks/update/{this.props.match.params.id}</a>
                            <br />
                            <pre />
                            <code>
                                
                                Pacote JSON<br />
                            {'{'}<br />
                                    "parkname": "{this.state.parkname}",<br />
                                    "parkinglots": {this.state.parkinglots},<br />
                                    "parkinglotsavailable": {this.state.parkinglotsavailable}<br />
                            {'}'}
                            </code>
                            </li>
                        </ol>
                    </div>
                    
                </form>
            </div>
        );
    }
}