import React, { Component } from 'react';
import { Link } from 'react-router-dom';

export default class Navbar extends Component {
    
    render(){
        return (
            <nav className="navbar navbar-dark bg-dark navbar-expand-lg">
                <Link to="/carparking/" className="navbar-brand">MICRO II - Projeto Denilson, Charles e Daniel</Link>
                <div className="collapse navbar-collapse">
                    <ul className="navbar-nav mr-auto">
                        <li className="navbar-item">
                            <Link to="/carparking/" className="nav-link">Estacionamentos</Link>
                        </li>
                        <li className="navbar-item">
                            <Link to="/carparking/create" className="nav-link">Criar estacionameto</Link>
                        </li>
                    </ul>
                </div>
            </nav>
        );
    }
};